.widget-wrapper-container {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: 2px solid rgba(0, 0, 0, 0.527);
    position: relative;
}

.widget-content {
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}


.react-grid-item.react-grid-placeholder {
    background: rgb(0, 187, 255);
    border-radius: 10px;
}
/* dims the rest of the screen when we enter a form */

/* Generic button style */
.generic-button {
    background: transparent; 
    border: 2px solid transparent; 
    border-radius: 50%; 
    font-size: 20px; 
    color: black; 
    cursor: pointer;
    width: 40px; 
    height: 40px; 
    padding: 0; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    transition: all 0.3s ease; 
}

/* Hover effect */
.generic-button:hover {
    color: white; 
    background-color: rgb(47, 47, 47); 
}

/* Specific styling for edit button */
.edit-button {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 12;
}

/* Specific styling for close button */
.close-button {
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 1000;
}